<template>
  <div class="singleBlog" v-if="blog">
    <app-header :title="blog.title" />

    <router-link to="/" class="btn-back">Back to home</router-link>

    <div v-if="blog" class="row">
        <img src="../assets/wave.svg" alt="wave" class="img-wave">
        <div class="card">
            <div class="card__image-container">
                <img class="card__image" :src="blog.image" alt="blog image" />
            </div>

            <svg class="card__svg" viewBox="0 0 800 500">
                <path d="M 0 100 Q 50 200 100 250 Q 250 400 350 300 C 400 250 550 150 650 300 Q 750 450 800 400 L 800 500 L 0 500" stroke="transparent" fill="#333"/>
                <path class="card__line" d="M 0 100 Q 50 200 100 250 Q 250 400 350 300 C 400 250 550 150 650 300 Q 750 450 800 400" stroke="pink" stroke-width="3" fill="transparent"/>
            </svg>

            <div class="card__content">
                <h1 class="card__title">{{blog.title}}</h1>
                <p>{{blog.summary}}</p>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import appHeader from "@/components/app-header";
import axios from "axios";
export default {
  name: "singleBlog",
  data() {
    return {
      blog: null
    };
  },
  components: {
    appHeader
  },
  mounted() {
    axios
      .get(`http://localhost:3000/blog/${this.$route.params.id}`)
      .then(res => {
        this.blog = res.data;
      });
  }
};
</script>

<style lang="scss" scoped>
* {
    box-sizing: border-box;
    line-height: 1.5;
    font-family: 'Open Sans', sans-serif;
}
.btn-back{
    position: absolute;
    top: 20px;
    left: 20px;
    background: rgba(255, 255, 255, .2);
    z-index: 22222;
    color: #fff;
    padding: 10px 15px;
    border-radius: 10px;
    text-decoration: none;
    transition: all .5s ease;
    &:hover{
        background: rgba(255, 255, 255, .3);
    }
}
.row {
     background: #3C3B3F;  /* fallback for old browsers */
    background: #cc2b5e;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to bottom, #753a88, #cc2b5e);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to bottom, #753a88, #cc2b5e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}
img {width: 100%}
.img-wave{
    position: absolute;
    top: 15px;
    opacity: 0.4;
    transform: rotate(180deg);
    @media (max-width: 500px) {
      top: 185px
    }
}
.card {
  height: 600px;
  margin: 3rem 1em;
}
 @media (max-width: 500px) {
    .card {height:auto}
    .card__image-container{
      height: 240px;
      .card__image{
        height: 100%;
      }
    }
  }
</style>
