<template>
  <div class="home">
    <app-header :title ="title"/>
    <div class="bg-image">
    <div class="container">
      <div class="row"  v-if="blogs">
        <!-- v-for="(blog, index) in blogs"
        :key="index""-->
        <blogCard 
        v-for="blog in blogs" :key="blog.id"
        :data = "blog"
        
        />
    
      </div>
      
    </div>
    <!-- End container -->
    <div class="text-center">
        <button 
        type="button" 
        class="btn-loadMore"
        @click="loadMore()"
        v-if="blogs && blogs.length < totalPosts">
        Load More</button>
    </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
 import appHeader from '@/components/app-header'
 import blogCard from '@/components/blog-card'
 import axios from 'axios';

export default {
  name: 'home',
   data() {
    return {
      title:"blog",
      blogs: null,
      page:1,
      totalPosts: null
    }
  },
  components:{
    appHeader,
    blogCard
  },
  mounted() {
    this.getPosts(this.page);
  },
  methods: {
    getPosts(page){
      axios.get(`http://localhost:3000/blog?_page=${page}&_limit=6`).then((res)=>{
          this.blogs= this.blogs? this.blogs.concat(res.data): res.data;
          this.totalPosts = res.headers['x-total-count'];
          // console.log(this.totalPosts);
          
        })
    },
    loadMore(){
      this.page++;
      this.getPosts(this.page);
    }
  },

}
</script>




