.bg-image{
  background: url('../assets/bg.jpg');
  background-size: cover;
  padding-top: 30px;

}
.text-center{
  text-align: center;

  .btn-loadMore{
    padding:12px 40px;
    background: #9e1d91;
    margin-bottom:50px;
    border:none;
    border-radius: 20px;
    cursor: pointer;
    color:#FFF;
    font-size: 16px;
    transition: all .5s ease;
    outline: none;
    &:hover{
      background: #4b0644;
    }
  }

}
